.album-container {
	/* #a2a633 */
	background-color: #c9cd65;
	min-height: 32vh;
	box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.2);
	text-align: center;
	border-radius: 1rem;
	display: flex;
	flex: 1 1;
	flex-wrap: wrap;
	justify-content: center;
	margin-top: 20px;
	/* margin: 20px; */
	/* font-family: 'IBM Plex Mono', monospace;
	font-weight: 100; */
}

.album-image {
	width: 100%;
	height: 70%;
}

.video {
	height: 4rem;
	width: 100%;
}

.song-title,
.album-title {
	font-family: 'IBM Plex Mono', monospace;
	font-weight: 500;
	font-size: 1.2rem;
}

@media only screen and (max-width: 660px) {
	.album-container {
	}

	.bottom {
		height: 160px;
	}
}

@media only screen and (max-width: 460px) {
	.album-container {
		width: 80vw;

		margin: 0 auto;
	}

	.bottom {
		height: 160px;
	}
}

body {
	/* background-color: #76786d; */
	background: linear-gradient(to left, #76786d, rgb(73, 80, 80) 100%) no-repeat center fixed;

	/* background-size: auto;
	background-repeat: space; */
}

.search-container {
	/* display: flex; */
	justify-content: center;
	margin: 0 auto;
	/* display: -webkit-box; */
	display: flex;
	/* -webkit-box-orient: vertical;
	-webkit-box-direction: normal; */
	flex-direction: row;
	width: 500px;
	height: 52px;
	width: 100%;
	margin-top: 30px;
}

.submit {
	background: black;
	border: 1px solid black;
	color: #fff;
	padding: 0 30px;
	cursor: pointer;
	transition: all 0.2s;
	font-size: 1.4rem;
}

input[type="submit"]:hover {
	background: #d73851;
	border: 1px solid #d73851;
}

input[type="search"] {
	flex-basis: 25%;
	font-size: 1.4rem;
}

@media only screen and (max-width: 460px) {
	.search-container {
		width: 70vw;
	}

	input[type="search"] {
		font-size: 1rem;
	}
	.submit {
	}
}

@media only screen and (max-width: 360px) {
	.search-container {
		flex-direction: column;
		width: 70vw;
	}

	input[type="search"] {
		height: 5vh;
		font-size: 1rem;
	}
	.submit {
		margin-top: 10px;
	}
}

@media only screen and (max-width: 300px) {
	.search-container {
		width: 80vw;
	}
}

/* font-family: 'IBM Plex Mono', monospace;
font-family: 'Montserrat', sans-serif;
font-family: 'Roboto', sans-serif; */

.artist-container {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 50px;
}

.band-title {
	color: white;
	font-size: 3rem;
	font-family: 'IBM Plex Mono', monospace;
	letter-spacing: -1px;
	text-transform: uppercase;
	font-weight: 100;
}

.artist-info {
	display: flex;
	flex-direction: column;
	align-items: center;
	line-height: 10px;
	font-family: 'IBM Plex Mono', monospace;
	color: rgb(4, 4, 4);
	text-transform: uppercase;
	font-weight: 100;
	font-size: 1.5rem;
	margin: 0 auto;
}

.h3 {
	/* font-size: 1.5rem; */
}

.band-image {
	width: 30%;
	border-radius: 50%;
	box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.2);
	margin: 0 auto;
}

.popular {
	display: flex;
	margin-bottom: 50px;
	margin-top: 50px;
	justify-content: center;
	/* margin: auto 0; */
	font-family: 'IBM Plex Mono', monospace;
	text-transform: uppercase;
	font-weight: 100;
	font-size: 3rem;
	color: white;
}

@media only screen and (max-width: 1130px) {
	.band-title {
		font-size: 2rem;
	}

	.artist-info {
		font-size: 1rem;
	}
}

@media only screen and (max-width: 840px) {
	.band-title {
		font-size: 1.5rem;
	}

	.artist-info {
		font-size: .9rem;
	}
}

@media only screen and (max-width: 585px) {
	.band-title {
		font-size: .9rem;
	}

	.artist-info {
		font-size: .7rem;
		width: 30vw;
	}

	.popular {
		font-size: 2rem;
	}
}

@media only screen and (max-width: 540px) {
	.band-title {
		font-size: .9rem;
	}

	.artist-info {
		font-size: .7rem;
		width: 40vw;
	}

	.main-container {
		width: 100vw;
	}
}

@media only screen and (max-width: 406px) {
	.artist-info {
		font-size: .7rem;
		width: 55vw;
	}

	.artist-container {
		width: 70%;
		margin: 20px auto;
	}
}

@media only screen and (max-width: 360px) {
	.popular {
		font-size: 1.5rem;
	}
}

@media only screen and (max-width: 300px) {
	.band-image {
		margin-top: 2rem;
		width: 40vw;
	}

	.artist-container {
		width: 60%;
		margin: 20px auto;
		flex-direction: column;
	}
}

.main-container {
	max-width: 2600px;
	/* width: 100vw; */
	justify-content: center;
	display: flex;
	justify-content: center;
	align-items: center;
}

html,
body {
	overflow-x: hidden;
}

@media only screen and (max-width: 550px) {
	.main-container {
		/* display: grid;
		grid-template-columns: auto; */
	}
}

